/* css - untuk tabel */

#users {
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 650px;
}

#users td, #users th {
    border: 1px solid #ddd;
    padding: 8px;
}

#users tr:nth-child(even){ 
    background-color: #f2f2f2;
}

#users tr:hover { 
    background-color: #ddd;
}

#users th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #4CAF50;
    color: white;
}


/* css - untuk form */

h3 {
    margin-bottom: 5px;
    color: gray;
}

input[type=text], textarea {
    width: 650px;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

input[type=submit] {
    width: 650px;
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

input[type=submit]:hover {
    background-color: #45a049;
}

/* css - notifikasi */

.warning-message, .success-message {
    width: 650px;
    padding: 10px;
    margin-top: 20px;
    box-sizing: border-box;
    text-align: center;
}

.warning-message {
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
}

.success-message {
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb;
}